@font-face {
  font-family: 'iconfont';  /* project id 330811 */
  src: url('//at.alicdn.com/t/font_tjw0enulvg8n0zfr.eot');
  src: url('//at.alicdn.com/t/font_tjw0enulvg8n0zfr.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_tjw0enulvg8n0zfr.woff') format('woff'),
  url('//at.alicdn.com/t/font_tjw0enulvg8n0zfr.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_tjw0enulvg8n0zfr.svg#iconfont') format('svg');
}

.iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    vertical-align: baseline;
}

.null-data{
	text-align: center;
	padding: 1.5rem 0.75rem;
	color: @fc-lighter;
	&:before{
		display: block;
		content: '\e623';
		font-family: 'iconfont';
		font-size: 5rem;
		color: @fc-lighter;
		line-height: 1;
	}
	&.hide{
		display: none;
	}
	span{
		margin-top: 0.5rem;
		display: block;
	}
}

.header-bar ~ .content{
	padding-top: 2.2rem;
}
.mint-indicator-wrapper{
	z-index: 999;
}


