.bannerBox {
  width: 100%;
  height: 30vw;
  overflow: hidden;
  position: relative;
}
.bannerBox .swiper-slide {
  width: 100%;
  height: 100%;
}
.bannerBox .swiper-slide .swiper-img-link {
  display: block;
  width: 100%;
  height: 100%;
}
.bannerBox .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
}
.bannerBox .swiper-pagination {
  bottom: 5px!important;
}
.bannerBox .swiper-pagination .swiper-pagination-bullet {
  background: #000;
  opacity: 0.5;
}
.bannerBox .swiper-pagination .swiper-pagination-bullet-active {
  background: #ff7551;
  opacity: 0.8;
}



