.bannerBox {
  width: 100%;
  height: 30vw;
  overflow: hidden;
  position: relative;
}
.bannerBox .swiper-slide {
  width: 100%;
  height: 100%;
}
.bannerBox .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
}
.bannerBox .swiper-pagination-progress {
  top: 100%;
  margin-top: -4px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 2px;
  overflow: hidden;
}
.bannerBox .swiper-pagination-progress .swiper-pagination-progressbar {
  background: #1cd39b;
  opacity: 0.8;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}



