.header {
  position: absolute;
  width: 100%;
  height: 2rem;
  background: #fff;
  line-height: 2rem;
  top: 0;
  left: 0;
  z-index: 21;
}
.header .header-nav {
  width: 100%;
  height: 2rem;
  background: #fff;
  line-height: 2rem;
  text-align: center;
}
.header .header-nav .nav-a {
  display: inline-block;
  color: #333;
  padding: 0 0.1rem;
  font-size: 0.75rem;
}
.header .header-nav .nav-a.router-link-active {
  position: relative;
}
.header .header-nav .nav-a.router-link-active:after {
  display: block;
  content: "";
  width: 100%;
  height: 0.1rem;
  background: #1cd39b;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 0.05rem;
}
.header .nav-icon {
  position: absolute;
  top: 0;
  padding: 0 0.5rem;
  height: 2rem;
  line-height: 2rem;
  color: #1cd39b;
  font-size: 0.8rem;
}
.header .nav-serach {
  left: 0rem;
}
.header .nav-msg {
  right: 0;
}



