
@import './assets/css/reset.css';
@import './assets/css/variable.less';
@import './assets/css/public.less';
@import './assets/css/sui.css';
body{
	width: 100%;
	height: 100%;
}
.page{
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}
.content{
position: absolute;
width: 100%;
top: 0;
bottom: 0;
overflow: scroll;
}
.footer ~ .content{
    bottom: 2rem;
}
.header ~ .content{
    top: 2rem;
}



